body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-image: url("https://cdn2.vectorstock.com/i/thumb-large/90/61/tic-tac-toe-xo-game-seamless-pattern-black-board-vector-6779061.jpg");
    background-size: repeat;
  }
  
.status{
    color: yellow;
}

  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    justify-content: center;
    flex-direction: row;
    background-color: rgba(25, 25, 100, .9);
    border: 5px solid rgb(0, 77, 128);
    border-radius: 25px;
    padding: 5%;
    margin-top: 10%;
    margin-left: 25%;
    margin-right: 25%;
  }
  
  .game-info {
    margin-left: 20px;
  }
  